* {
    box-sizing: border-box;
}

body {
background-color: white;
}

#scan {
    background-color: white;
    padding: 0px;
    border: hidden;
    margin-top: 0px;
    padding-top: 0px;
}

#search, #checkOut {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: white;
}

#title {
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: 0px;
    margin-bottom: 1rem;
    margin-left: auto;
    margin-right: auto;
    border: hidden;
    font-size: 1.5rem;
}

#logo {
    height: 3rem;
    margin-right: 1rem;
    background-color: #ffffff;
    float: left;
}

#contents {
    overflow: hidden;
    height: auto;
    width: 100% - 2rem;
    padding: 0;
    margin: 0;
    margin-left: 1rem;
    margin-right: 1rem;
}

#reader {
    background-color: #ffffff;
    text-align: center;
    margin: 0;
    width: 50%;
    padding: 0;
    float: left;
}

#recent {
    background-color: #ffffff;
    text-align: center;
    margin: 0px;
    width: 50%;
    float: left;
    padding-top: 1rem;
    padding-bottom: 1rem;
}

#searchContents, #checkOutInput, #checkOutResult, #checkOutUser {
    width: 100%;
    font-size: 1.0rem;
    padding: 1rem;
}

#checkOutUser {
    display: flex;
    width: 100%;
    padding: 0;
    border: hidden;
}

#name, #logOut {
    font-size: 1.2rem;
    outline: none;
    box-sizing: border-box;
    margin: 0;
    position: relative;
    float: left;
}

#name {
    text-align: left;
    padding-top: 15px;
    width: 100%;
    margin-left: 5px;
}

#logOut {
    float: right;
    min-width: 8rem;
    padding: 0;
    border: hidden;
}

#searchInput, #searchPassword, #logIn, #logOutButton {
    width: 100%;
    font-size: 1.1rem;
    border: 2px solid lightgray;
    border-radius: 1rem;
    outline: none;
    padding: 1rem;
    box-sizing: border-box;
    text-align: center;
}

#logOutButton {
    float: right;
    min-width: 8rem;
    border: 2px solid lightgray;
    margin-bottom: 5px;
    padding: 8px;
    font-size: 1.2rem;
}

.button {
    width: 100%;
    font-size: 1.1rem;
    border: 2px solid lightgray;
    border-radius: 1rem;
    outline: none;
    padding: 1rem;
    box-sizing: border-box;
    text-align: center;
}
#searchResult {
    font-size: 1.0rem;
    width: 100%;
    margin-top: 0.5rem;
    border: 1px solid lightgray;
    border-radius: 0.5rem;
    padding: 0.5rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
}

#selectedSearchResult {
    font-size: 1.0rem;
    width: 100%;
    margin-top: 0.5rem;
    border: 1px solid lightgray;
    border-radius: 0.5rem;
    padding: 0.5rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    background-color: #4080e0;
    color: white;
}

#searchResult:active {
    background-color: #4080e0;
    color: white;
}

*:disabled {
    background-color: #a0a0a0;
    color: black;
}

#selected {
    width: max(calc(100vw - 30rem), 80%);
    box-sizing: border-box;
    padding: 1rem;
    display: grid;
    grid-template-columns:
        repeat(auto-fill, minmax(150px, 1fr));
    gap: 1rem;
}

#selectedItem {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#selectSearched, #selectAll, #removeAll {
    font-size: 1.0rem;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
    border-radius: 10px;
    padding: 0.5rem;
    box-sizing: border-box;
    width: min(30rem, 80%);
}

#pageControl {
    display: flex;
}

#prevPage, #nextPage {
    display: flex;
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
    border-radius: 10px;
    padding: 0.5rem;
    box-sizing: border-box;
    width: 100%;
    text-align: center;
}
#pageNum {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
    border-radius: 10px;
    padding: 0.5rem;
    width: 5rem;
    text-align: center;
}

table {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

th {
    height:3em;
    width:20%;
    font-size: 100%;
    color: white;
    background-color: #808080;
}

th:nth-child(1) {
    width:10%;
}

th:nth-child(2) {
    width:50%;
}

tr {
    width:100%;
    font-size: 120%;
    background-color: #ffffff;
    text-align: center;
    border: solid 2px black;
    margin-top: 0.5em;
}

td {
    border: solid 2px lightgray;
    height: 3em;
    margin-top: 1.5em;
}

tr.searchTr {
    width: 100%;
    margin: 0.5rem;
    margin-top: 1rem;
    background: rgba(0,0,0,0);
}

td.searchTitle {
    border: hidden;
    height: 2em;
    width:70%;
}
td.searchRent {
    border: hidden;
    height: 2em;
    width:30%;
}
td.page {
    border: hidden;
    height: 2em;
    width:5em;
}

.menu tr:hover {
    font-size: 120%;
    background-color: #ffc0c0;
    text-align: center;
}

@media only screen and (max-width: 900px) {
    h2 {
    margin-top: 0.5rem;
    margin-bottom: 0px;
    }
    #title {
        margin: 0.5rem;
        font-size: 1.0rem;
    }
    #reader {
        width: 100%;
    }
    #recent {
        width: 100%;
        padding-top: 0rem;
        margin-top: 0rem;
        padding-bottom: 0rem;
    }
    #clock {
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
    }
    #searchContents {
        width: 100%;
        margin-left: 0.12em;
        margin-right: 0.12em;
    }
    #checkOutResult {
        font-size: 0.8rem;
    }
}

