.navbar {
    position: relative;
    background-color: white;
    height: 65px;
    display: flex;
    font-size: 10px;
    box-shadow: 1px 2px 5px lightgray;
}

.img {
    height: 1em;
    position: relative;
    top: 10px;
}

.menu {
    color: #fff;
    cursor: pointer;
    text-decoration: none;
    font-size: 35px;
    margin-right: 1rem;
}

.title {
    width: 100%;
    background-color: #ffffff;
    display: flex;
    justify-content: left;
    flex-direction: row;
    margin-left: 1rem;
    border: hidden;
    font-size: 1.2rem;
    text-align: left;
    padding-top: 5px;
}

@media screen and (max-width: 1000px) {
    .menu {
        margin-right: 1rem;
    }

    #logo {
        display: none;
    }
    .navbar {
        position: relative;
        background-color: white;
        height: 65px;
        display: flex;
        justify-content: left;
        align-items: center;
        font-size: 20px;
        box-shadow: 1px 2px 5px lightgray;
    }
    .title {
        width: 100%;
        background-color: #ffffff;
        display: flex;
        border: hidden;
        font-size: 1.1rem;
        text-align: left;
    }
    .img {
        top: 5px;
    }
}


